import PropTypes from "prop-types";
import { useState } from "react";
import { format } from "date-fns";
// @mui
import { useTheme } from "@mui/material/styles";
import {
  TableRow,
  Checkbox,
  TableCell,
  Typography,
  MenuItem,
} from "@mui/material";
// utils
import { fDateTime } from "../../../../utils/formatTime";
import { fCurrency } from "../../../../utils/formatNumber";
// components
import Iconify from "../../../../components/Iconify";
import { TableMoreMenu } from "../../../../components/table";
import Label from "../../../../components/Label";
import { useTranslation } from "react-i18next";
//

// ----------------------------------------------------------------------

UserCommissionTableRow.propTypes = {
  row: PropTypes.object,
  selected: PropTypes.bool,
  onEditRow: PropTypes.func,
  onSelectRow: PropTypes.func,
  onDeleteRow: PropTypes.func,
  deleteText: PropTypes.string,
  editText: PropTypes.string,
};

export default function UserCommissionTableRow({
  row,
  selected,
  onEditRow,
  onSelectRow,
  onDeleteRow,
  deleteText = "Delete",
  editText = "Edit",
}) {
  const theme = useTheme();

  const { t } = useTranslation();

  const [openMenu, setOpenMenuActions] = useState(null);

  const handleOpenMenu = (event) => {
    setOpenMenuActions(event.currentTarget);
  };

  const handleCloseMenu = () => {
    setOpenMenuActions(null);
  };

  return (
    <TableRow hover selected={selected}>
      <TableCell padding="checkbox" />

      <TableCell align="left">{row?.ref_code}</TableCell>

      <TableCell align="center">{row?.full_name || "-"}</TableCell>
      <TableCell align="center">{row?.type_transaction || "-"}</TableCell>
      <TableCell align="center">{fDateTime(row?.created_on)}</TableCell>
      <TableCell align="center">{fCurrency(row?.amount)} {row?.currency}</TableCell>
      <TableCell align="center">{fCurrency(row?.newAmount)} {row?.currency}</TableCell>

      <TableCell align="center">
        <Iconify
          icon={
            row.transaction_status === "COMPLETE"
              ? "eva:checkmark-circle-fill"
              : row.transaction_status === "PENDING"
              ? "eva:clock-outline"
              : row.transaction_status === "FAILED"
              ? "eva:close-circle-fill"
              : ""
          }
          sx={{
            width: 20,
            height: 20,
            color: "success.main",
            ...(row.transaction_status === "PENDING" && {
              color: "warning.main",
            }),
            ...(row.transaction_status === "FAILED" && { color: "error.main" }),
          }}
        />
      </TableCell>
    </TableRow>
  );
}
