import PropTypes from "prop-types";
// @mui
import {
  Tooltip,
  IconButton,
  Stack,
  InputAdornment,
  TextField,
  MenuItem,
} from "@mui/material";
import { LoadingButton } from "@mui/lab";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";
// components
import Iconify from "../../../../components/Iconify";
import { useTranslation } from "react-i18next";

// ----------------------------------------------------------------------
const INPUT_WIDTH = 160;

FeeListTableToolbar.propTypes = {
  isLoading: PropTypes.bool,
  filterUser: PropTypes.string,
  filterEndDate: PropTypes.instanceOf(Date),
  filterStartDate: PropTypes.instanceOf(Date),
  filterName: PropTypes.string,
  placeholder: PropTypes.string,
  loadText: PropTypes.string,
  title: PropTypes.string,
  onFilterName: PropTypes.func,
  onFilterEndDate: PropTypes.func,
  onFilterUser: PropTypes.func,
  onFilterStartDate: PropTypes.func,
  users: PropTypes.array,
  handleRefresh: PropTypes.func,
};

export default function FeeListTableToolbar({
  users,
  filterName,
  filterUser,
  filterStartDate,
  filterEndDate,
  onFilterName,
  handleRefresh,
  onFilterUser,
  onFilterStartDate,
  onFilterEndDate,
  isLoading,
  placeholder = "Search ...",
  loadText = "Load",
  title = "Filter list",
}) {
  const { t } = useTranslation();
  return (
    <Stack
      direction="row"
      alignItems="center"
      justifyContent="space-between"
      sx={{ py: 2.5, px: 3 }}
    >
      <TextField
        value={filterName}
        onChange={(event) => onFilterName(event.target.value)}
        placeholder={placeholder}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <Iconify
                icon={"eva:search-fill"}
                sx={{ color: "text.disabled", width: 20, height: 20 }}
              />
            </InputAdornment>
          ),
        }}
      />
      <TextField
        fullWidth
        select
        label={t("field.user")}
        value={filterUser}
        onChange={onFilterUser}
        SelectProps={{
          MenuProps: {
            sx: { "& .MuiPaper-root": { maxHeight: 260 } },
          },
        }}
        sx={{
          textTransform: "capitalize",
          maxWidth: { md: INPUT_WIDTH },
        }}
      >
        <MenuItem
          key="ALL"
          value=""
          sx={{
            mx: 1,
            my: 0.5,
            borderRadius: 0.75,
            typography: "body2",
            textTransform: "uppercase",
            fontWeight: "bold",
          }}
        >
          {t("params.all")}
        </MenuItem>
        {users?.map((usr) => (
          <MenuItem
            key={usr.uid}
            value={usr.uid}
            sx={{
              mx: 1,
              my: 0.5,
              borderRadius: 0.75,
              typography: "body2",
              textTransform: "capitalize",
            }}
          >
            {usr.full_name}
          </MenuItem>
        ))}
      </TextField>

      <DatePicker
        label={t("field.start_date")}
        value={filterStartDate}
        onChange={onFilterStartDate}
        renderInput={(params) => <TextField {...params} fullWidth />}
      />

      <DatePicker
        label={t("field.end_date")}
        value={filterEndDate}
        onChange={onFilterEndDate}
        renderInput={(params) => <TextField {...params} fullWidth />}
      />

      <Tooltip title={title}>
        <span>
          <LoadingButton
            startIcon={<Iconify icon={"ic:round-filter-list"} />}
            variant="contained"
            onClick={handleRefresh}
            loading={isLoading}
          >
            {loadText}
          </LoadingButton>
        </span>
      </Tooltip>
    </Stack>
  );
}
